import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import styled from "styled-components";
// import { Modal } from "react-bootstrap";
import { notify } from "../../../../_helpers/notify";
import eth_icon from "../../app-assets/images/coin/eth.png";
import usdt_icon from "../../app-assets/images/coin/usdt.png";
import xrp_icon from "../../app-assets/images/coin/xrp.png";
import eos_icon from "../../app-assets/images/coin/eos.png";
import btc_icon from "../../app-assets/images/coin/btc.png";
import bnb_icon from "../../app-assets/images/coin/bnb.png";

// import qr_code_icon from "../../app-assets/images/page/wallet/qr-code.png";

// import FiatCurrency from "../../components/input/FiatCurrency.component";
import { useTranslation } from "react-i18next";
import useServiceContextHook from "../../../../_hooks/service.context.hook";
import usePaginator from "../../../../_hooks/paginator.hook.js";

const currencyImages = {
  BTC: btc_icon,
  ETH: eth_icon,
  USDT: usdt_icon,
  XRP: xrp_icon,
  EOS: eos_icon,
  BNB: bnb_icon,
};

// Create our number formatter.
// var formatter = new Intl.NumberFormat("en-US", {
//   style: "currency",
//   currency: "USD",

//   // These options are needed to round to whole numbers if that's what you want.
//   //minimumFractionDigits: 0, // (this suffices for whole numbers, but will print 2500.10 as $2,500.1)
//   //maximumFractionDigits: 0, // (causes 2500.99 to be printed as $2,501)
// });
const tabButton = styled.button``;

export default function Wallet() {
  const pageTabs = {
    assets: {
      label: "Assets",
      callback(cb) {
        cb();
      },
    },
    history: {
      label: "History",
      callback: (cb = null) => cb,
    },
  };
  const { t } = useTranslation();
  const {
    services: { wallet: walletService, util },
  } = useServiceContextHook();
  const [currencies, setCurrencies] = useState();
  const [assets, setAssets] = useState();
  const [assetHistory, setAssetsHistory] = useState();
  const [isLoading, setIsLoading] = useState(false);
  const [params /*  setParams */] = useState({ fake: false });
  const [activePageTab, setActivePageTab] = useState("assets");

  const {
    count,
    page,
    limit,
    setCount,
    onRowsPerPageChange,
    onPageChange,
    StyledPagination,
  } = usePaginator();

  useEffect(() => {
    /**
     * @description update wallet
     * @returns
     */
    async function fetchAssets() {
      try {
        setIsLoading(true);

        let wallets = await walletService.find({
          limit,
          offset: page * limit,
          // fake: true,
        });
        const { error, data } = wallets;
        if (error) {
          console.log("error", error);
          return;
        }

        setCount(data?.count);
        setAssets(data?.result);
      } catch (err) {
        notify(err.message, "error");
      } finally {
        setIsLoading(false);
      }
    }

    fetchAssets();

    (async () => {
      let response = await util.findByName("CRYPTO_CURRENCIES");

      let { error, data } = response;
      if (error) {
        return;
      }
      setCurrencies(data);
    })();

    return () => {
      walletService?.abort();
    };
  }, [count, page, limit, walletService]);

  useEffect(() => { }, [params]);
  
  async function fetchHistory() {
    try {
    } catch (err) {
      notify(err.message, "error");
    }
  }

  return (
    <div className="cointainer">
      <section id="mainTop">
        <div className="container">
          <div className="row">
            <div className="col-12">
              <h3
                className="wow animate__animated fadeInDown"
                data-wow-delay="0.3s"
              >
                {t("Wallet")}
              </h3>
            </div>
          </div>
        </div>
      </section>
      {!isLoading && assets ? (
        <>
          <section id="progress">
            <div className="container">
              <div className="row wow fadeInUp" data-wow-delay="0.5s">
                <div className="col-lg-4 col-md-4 col-sm-12">
                  <dl>
                    <dt>{t("All balance")}</dt>
                    <dd>
                      <span>0.00000000</span>BTC
                    </dd>
                    <dd> $0.000000</dd>
                  </dl>
                </div>
                <div className="col-lg-4 col-md-4 col-sm-12">
                  <dl>
                    <dt>{t("Available balance")}</dt>
                    <dd>
                      <span>0.00000000</span>BTC
                    </dd>
                    <dd> $0.000000</dd>
                  </dl>
                </div>
                <div className="col-lg-4 col-md-4 col-sm-12">
                  <dl>
                    <dt>{t("Balance in process")}</dt>
                    <dd>
                      <span>0.00000000</span>BTC
                    </dd>
                    <dd> $0.000000</dd>
                  </dl>
                </div>
              </div>
            </div>
          </section>

          <section id="asset">
            <div className="container">
              <div className="row wow fadeInUp" data-wow-delay="0.7s">
                <div className="col-12 clear">
                  <h4>{t("Asset")}</h4>
                  <ul className="clear">
                    {Object.entries(pageTabs).map(
                      ([id, { label, callback }]) => (
                        <li
                          onClick={() => setActivePageTab(id)}
                          className={`${id == activePageTab && "on"}`}
                          key={id}
                        >
                          <button type="button">{t(label)}</button>
                        </li>
                      )
                    )}
                  </ul>
                  <div className="table_container">
                    {assets && currencies ? (
                      <table>
                        <thead>
                          <tr>
                            <th>{t("Coin")}</th>
                            <th>{t("Available")}</th>
                            <th>{t("In Order")}</th>
                            {/* <th>{t("USD Value")}</th> */}
                            <th>{t("Action")}</th>
                          </tr>
                        </thead>
                        <tbody>
                          {assets.length ? (
                            assets?.map((data) => (
                              <tr
                                key={data.id}
                                className={String(
                                  currencies[data.currency]
                                )?.toLowerCase()}
                              >
                                <td className="coin clear">
                                  <img
                                    src={
                                      currencyImages[data.currency] || btc_icon
                                    }
                                    alt={data.currency}
                                  />
                                  <dl>
                                    <dt>{currencies[data.currency]}</dt>
                                    <dd>{data.currency}</dd>
                                  </dl>
                                </td>
                                <td className="available">
                                  {Number(
                                    data.balance.availableBalance
                                  ).toFixed(18)}
                                </td>
                                <td className="order">
                                  {(
                                    Number(data.balance.accountBalance) -
                                    Number(data.balance.availableBalance)
                                  ).toFixed(18)}
                                </td>
                                {/* <td className="value">
                                  {formatter.format(data.usd_value)}
                                </td> */}
                                <td className="action">
                                  <button
                                    type="button"
                                    // {`?address=${data.address}&action=deposit`}
                                  >
                                    Deposit
                                  </button>
                                  <button
                                    type="button"
                                    // to={`?address=${data.address}&action=withdraw`}
                                  >
                                    Withdraw
                                  </button>
                                  <button
                                    type="button"
                                    // to={`?address=${data.address}&action=trade`}
                                  >
                                    Trade
                                  </button>
                                </td>
                              </tr>
                            ))
                          ) : (
                            <tr>
                              <td colSpan={4}>Nothing found</td>
                            </tr>
                          )}
                        </tbody>
                        {walletService?.length ? (
                          <tfoot>
                            <tr>
                              <StyledPagination
                                count={count}
                                page={page}
                                rowsPerPage={limit || 10}
                                onRowsPerPageChange={onRowsPerPageChange}
                                onPageChange={onPageChange}
                              />
                            </tr>
                          </tfoot>
                        ) : null}
                      </table>
                    ) : null}
                  </div>
                </div>
              </div>
            </div>
          </section>

          <section id="guide">
            <div className="container">
              <div className="row wow fadeInUp" data-wow-delay="0.9s">
                <div className="col-12">
                  <p>
                    Transaction history can be checked in the Assets menu.
                    <br />
                    Detailed instructions on signup, verification and trading
                    can be found in the guide below..
                  </p>
                </div>
                <div className="col-lg-4 col-md-6 col-sm-12">
                  <Link to="#">
                    Exchange Guide<i className="far fa-arrow-to-bottom"></i>
                  </Link>
                </div>
                <div className="col-lg-4 col-md-6 col-sm-12">
                  <Link to="#">
                    pro chart guide<i className="far fa-arrow-to-bottom"></i>
                  </Link>
                </div>
                <div className="col-lg-4 col-md-12 col-sm-12">
                  <Link to="#">
                    Verification guide to change account information
                  </Link>
                </div>
                <div className="col-12">
                  <p>
                    For more information <span>FAQ</span>can be found at.
                  </p>
                </div>
              </div>
            </div>
          </section>
        </>
      ) : (
        "Loading..."
      )}
    </div>
  );
}

function Assets() {
  return <>Assets</>;
}
function History() {
  return <>History</>;
}
