function ModifyProfile({ formData = {} }) {
  const {
    services: { profile },
  } = useServiceContextHook();

  const initialValues = {
    lname: formData?.lname || "",
    oname: formData?.oname || "",
    pname: formData?.pname || "",
    // country: formData?.country || "",
    gender: formData?.gender || "",
    date_of_birth: formData?.date_of_birth || "",
  };

  function validate(values) {
    const errors = {};

    if (!values?.date_of_birth) {
      errors.dob = `Required field`;
    } else {
      if (!new Date(values?.date_of_birth)) errors.dob = `invalid date`;
    }

    if (!values?.lname) {
      errors.lname = `Required field`;
    } else {
      if (values?.lname.length < 2)
        errors.lname = `Must be at least (2) characters long`;
    }

    if (!values?.oname) {
      errors.oname = `Required field`;
    } else {
      if (values?.oname.length < 2)
        errors.oname = `Must be at least (2) characters long`;
    }

    /*  if (!values?.country) {
      errors.country = `Required field`;
    } */

    return errors;
  }
  async function onSubmit(values, { setSubmitting }) {
    try {
      let { data, error, message } = await profile.update(values);
      if (!data) throw new Error(error?.message || message);
      notify("Profile updated successfully!");
    } catch (err) {
      notify(err.message, "error");
    }
  }
  return (
    <Formik {...{ initialValues, validate, onSubmit }}>
      {({
        values,
        isSubmitting,
        setFieldValue,
        errors,
        touched,
        handleChange,
        handleBlur,
        handleSubmit,
      }) => (
        <form onSubmit={handleSubmit}>
          <div
            style={{
              display: "flex",
              gap: 10,
              flexDirection: "column",
              flex: "1",
            }}
          >
            {/* NICK NAME */}
            <label>Nickname</label>
            <FormControl
              placeholder="Nickname"
              onChange={handleChange}
              onBlur={handleBlur}
              defaultValue={values?.pname}
              required
              name="pname"
              type="text"
            />

            {/* LAST NAME */}
            <label>Last name</label>
            <FormControl
              placeholder="Last name"
              onChange={handleChange}
              onBlur={handleBlur}
              defaultValue={values?.lname}
              required
              name="lname"
              type="text"
            />
            <small className="text-danger">
              {errors && touched && errors.lname}
            </small>
            {/* Other names */}
            <label>Other names</label>
            <FormControl
              placeholder="Other names"
              onChange={handleChange}
              onBlur={handleBlur}
              defaultValue={values?.oname}
              required
              name="oname"
              type="text"
            />
            <small className="text-danger">
              {errors && touched && errors.oname}
            </small>

            {/* DOB */}
            <label>Date of Birth (yyyy-mm-dd)</label>
            <FormControl
              placeholder="Date of Birth (yyyy-mm-dd)"
              max={new Date().toISOString().split("T")[0]}
              onChange={handleChange}
              onBlur={handleBlur}
              defaultValue={values?.date_of_birth}
              type="date"
              required
              name="date_of_birth"
            />
            <small className="text-danger">
              {errors && touched && errors.dob}
            </small>
            {/* GENDER */}
            <FormLabel component="legend">Gender</FormLabel>
            <RadioGroup
              defaultValue={values?.gender}
              onChange={handleChange}
              row
              aria-label="gender"
              onBlur={handleBlur}
              name="gender"
              id="gender"
            >
              <FormControlLabel
                value="female"
                control={<Radio />}
                label="Female"
              />
              <FormControlLabel value="male" control={<Radio />} label="Male" />
              <FormControlLabel
                value="other"
                control={<Radio />}
                label="Other"
              />
            </RadioGroup>
            <small className="text-danger">
              {errors && touched && errors.gender}
            </small>
            {/* COUNTRY */}
            {/*  <CountrySelector
              onChange={(v) => setFieldValue("country", v)}
              attributes={{
                name: "country",
                defaultValue: values?.country,
                className: "p-2",
                onBlur: handleBlur,
              }}
            />
            <small className="text-danger">
              {errors && touched && errors.country}
            </small> */}

            {/* SUBMIT BUTTON */}
            <button
              disabled={isSubmitting || Object.keys(errors)?.length}
              type="submit"
              className="btn btn-primary mt-auto"
            >
              Save
            </button>
          </div>
        </form>
      )}
    </Formik>
  );
}

function VerifyID() {
  const {
    services: { kyc, upload },
  } = useServiceContextHook();

  const initialValues = {
    front: null,
    rear: null,
    surface: null,
  };

  /*  async function fetchData() {
    try {
      let { data, error, message } = await kyc.find({
        "filter[type]": "ID",
      });
      if (!data)
        throw new Error(error?.message || message || "Error fetching kyc data");
      let kycData = data?.result[0];
      if (kycData) setData(kycData?.kyc?.id);
    } catch (err) {
      notify(err?.message, "error");
    }
  } */

  function validate(values) {
    const errors = {};
    return errors;
  }

  async function onSubmit(values, { setSubmitting }) {
    try {
      setSubmitting(true);
      let uploads = await Promise.all(
        Object.entries(values)?.map(async ([key, value]) => {
          let formData = new FormData();
          formData.append("file", value);
          let { data, error, message } = await upload.create(formData);
          if (!data) notify(`${key} upload unsuccessful! Try again`, "error");

          notify(`${key} upload Succeeded!`);

          return { upload_type: key, ...data };
        })
      );

      if (uploads) {
        let kycResponse = await kyc.create({
          type: "ID",
          uploads,
        });
        if (!kycResponse.data)
          throw new Error(
            kycResponse.error?.message ||
              kycResponse.message ||
              "Error creating ID KYC"
          );
        notify("ID KYC created successfully!");
      }
    } catch (err) {
      notify(err?.message, "error");
    } finally {
      setSubmitting(false);
    }
  }

  /* useEffect(() => {
    fetchData();
  }, []); */
  return (
    <Formik {...{ initialValues, validate, onSubmit }}>
      {({ values, isSubmitting, setFieldValue, handleSubmit }) => (
        <form onSubmit={handleSubmit} enctype="multipart/form-data">
          <div
            style={{
              display: "flex",
              gap: 10,
              flexDirection: "column",
              flex: "1",
            }}
          >
            Front surface
            <FileUpload
              altText="Upload front photo"
              onChange={([v]) => setFieldValue("front", v)}
              inputProps={{
                files: values?.front,
                id: "file-upload",
                name: "front",
              }}
            />
            <FileUpload
              altText="Uplaod rear photo"
              onChange={([v]) => setFieldValue("rear", v)}
              inputProps={{
                files: values?.rear,
                id: "file-upload",
                name: "rear",
              }}
            />
            <FileUpload
              altText="Upload surface photo"
              onChange={([v]) => setFieldValue("surface", v)}
              inputProps={{
                files: values?.surface,
                id: "file-upload",
                name: "surface",
              }}
            />
            Rear surface
                <FileUpload
              onChange={([v]) => setFieldValue("file", v)}
              inputProps={{
                files: values?.file,
                id: "file-upload",
                name: "file",
              }}
            />
            <TextField
              // required
              variant="outlined"
              id="outlined-required"
              label="Address"
            />

            {/* SUBMIT BUTTON */}
            <button
              disabled={isSubmitting}
              type="submit"
              className="btn btn-primary mt-auto"
            >
              Submit
            </button>
          </div>
        </form>
      )}
    </Formik>
  );
}

function VerifyMail({ formData = { data: {}, onChange: () => null } }) {
  const {
    services: { auth },
    session,
  } = useServiceContextHook();

  const emailBox = useRef();
  const [isSending, setSending] = useState(false);
  const initialValues = {
    email: "",
    code: "",
  };

  useEffect(() => {
    if (emailBox.current) emailBox.current.focus();
  }, []);

  async function sendOTP(email) {
    try {
      setSending(true);
      let { data, error, message } = await auth.send_otp(
        JSON.stringify({ id: session?.user?.id, email })
      );
      if (!data) throw new Error(error?.message || message);

      if (data?.status) {
        notify(`An OTP has been sent to your email address: ${email}`);
      } else notify(`Could not contact mail server`);
    } catch (err) {
      notify(err.message, "error");
    } finally {
      setSending(false);
    }
  }

  function validate(values) {
    const errors = {};
    if (!values?.code) errors.code = "Code is required";
    if (initialValues?.email === values?.email)
      errors.email = `${values?.email} is already in use!`;
    if (!values?.email) errors.email = "Email is required";
    return errors;
  }

  async function onVerify(values, { setSubmitting }) {
    try {
      setSubmitting(true);
      let { data, error, message } = await auth.verify_email(
        JSON.stringify(values)
      );
      if (!data)
        throw new Error(
          error?.message || message || "Operation not completed!"
        );

      if (data && data?.status) {
        formData.onChange(values);
        notify("Email confirmed successfully!");
      } else notify("Invalid code", "error");
    } catch (err) {
      notify(err.message, "error");
    } finally {
      setSubmitting(false);
    }
  }

  return (
    <Formik {...{ initialValues, onSubmit: onVerify, validate }}>
      {({
        values,
        isSubmitting,
        touched,
        errors,
        handleChange,
        handleBlur,
        isValid,
      }) => (
        <Form
          style={{
            display: "flex",
            flexDirection: "column",
            gap: 10,
          }}
        >
          {formData?.data?.email && (
            <div style={{ display: "flex", gap: 5 }}>
              <strong style={{ fontWeight: "bold" }}>Current:</strong>
              <span>{formData?.data?.email}</span>
            </div>
          )}
          <InlineInput>
            <input
              ref={emailBox}
              placeholder="Email address"
              type="email"
              name="email"
              onChange={handleChange}
              defaultValue={values?.email}
              onBlur={handleBlur}
              disabled={isSending}
            />
            <button
              onClick={() => sendOTP(values?.email, formData?.onChange)}
              type="button"
              disabled={isSending || errors?.email || !values?.email}
            >
              {isSending ? "Sending..." : "Send code"}
            </button>
          </InlineInput>
          <small className="text-danger">{errors && errors?.email}</small>

          <InlineInput>
            <input
              placeholder="OTP code"
              type="text"
              name="code"
              maxLength={6}
              onBlur={handleBlur}
              onChange={handleChange}
              defaultValue={values?.code}
            />

            <button type="submit" disabled={isSubmitting || !isValid}>
              {isSubmitting ? "Checking..." : "Confirm code"}
            </button>
          </InlineInput>
          <small className="text-danger">
            {errors && touched && errors?.code}
          </small>
        </Form>
      )}
    </Formik>
  );
}

function VerifyPhone(formData = { phone: "", onChange: () => null }) {
  const {
    services: { auth },
    session,
  } = useServiceContextHook();
  const phoneBox = useRef();
  const [isSending, setSending] = useState(false);
  const initialValues = {
    phone: "",
    code: "",
  };
  const [timer, setTimer] = useState(0);

  useEffect(() => {
    let timeout = null;

    if (timer) {
      timeout = setTimeout(() => {
        setTimer(timer - 1);
      }, 1000);
    }

    return () => {
      clearTimeout(timeout);
    };
  }, [timer]);

  useEffect(() => {
    if (phoneBox.current) phoneBox.current.focus();
  }, []);

  async function sendOTP(phone) {
    try {
      setSending(true);
      let { data, error, message } = await auth.send_otp(
        JSON.stringify({ id: session?.user?.id, phone, type: "phone" })
      );
      setTimer(60);

      if (!data) throw new Error(error?.message || message);

      if (!data?.errors.sms) {
        notify(`An OTP has been sent to ${phone}`);
      } else
        notify(data?.errors?.sms?.message || `Could not contact SMS server`);
    } catch (err) {
      notify(err.message, "error");
    } finally {
      setSending(false);
    }
  }

  async function onVerify(values, { setSubmitting }) {
    try {
      setSubmitting(true);
      let { data, error, message } = await auth.verify_phone(
        JSON.stringify(values)
      );
      if (!data)
        throw new Error(
          error?.message || message || "Operation not completed!"
        );

      if (data && data?.status) {
        notify("Email confirmed successfully!");
      } else notify("Invalid code", "error");
    } catch (err) {
      notify(err.message, "error");
    } finally {
      setSubmitting(false);
    }
  }

  return (
    <Formik
      {...{
        initialValues,
        onSubmit: onVerify,
      }}
    >
      {({
        values,
        isSubmitting,
        touched,
        handleChange,
        handleBlur,
        setFieldValue,
        handleSubmit,
        errors,
        isValid,
      }) => (
        <Form
          style={{ display: "flex", flexDirection: "column", gap: 10 }}
          onSubmit={handleSubmit}
        >
          <PhoneNumber
            {...{ defaultValue: values?.phone, name: "phone" }}
            ref={phoneBox}
            onChange={(v) => setFieldValue("phone", v)}
          />
          <Button
            onClick={() => sendOTP(values?.phone)}
            type="button"
            disabled={isSending || timer}
          >
            {timer ? `Retry in ${timer}s` : "Send code"}
          </Button>
          <FormControl
            placeholder="OTP code"
            type="text"
            name="code"
            maxLength={6}
            onBlur={handleBlur}
            onChange={handleChange}
            defaultValue={values?.code}
          />

          <Button type="submit" disabled={isSubmitting || !isValid}>
            {isSubmitting ? "Checking..." : "Confirm code"}
          </Button>
          <small className="text-danger">
            {errors && touched && errors?.code}
          </small>
          {/* <Button variant="contained">Save</Button> */}
        </Form>
      )}
    </Formik>
  );
}

